
































export default {
	data() {
		return {
			key: '',
			data: ''
		}
	},
	methods: {
		clearStorage(){
			uni.clearStorage();
			uni.getStorageInfo({
				success: (res) =>{
					if(res.keys == ''){
						uni.showToast({
							title:"清理成功",
							duration:600
						})
					}
				}
			})
		},
		getStorageInfo(){
			uni.getStorageInfo({
				success: (res) => {
					uni.showModal({
						title:'本地存储的所有key',
						content:'key:'+res.keys+''
					})
				}
			})
		},
		setStorage(){
			if(this.key == ''){
				uni.showModal({
					title:"数据保存失败",
					content:"key不能为空",
					showCancel:false
				})
			}else{
				uni.setStorage({
					key:this.key, //本地缓存内容对应的key
					data:this.data, // 需要存储的内容
					success: (res) => {
						uni.showModal({
							title:"数据保存成功",
							content:"",
							showCancel:false
						})
					}
				})
			}
		},
		getStorage(){
			if(this.key == ''){
				uni.showModal({
					title:"数据获取失败",
					content:"key不能为空",
					showCancel:false
				})
			}else{
				uni.getStorage({
					key:this.key, //根据本地缓存对应key来获取对应的内容
					success: (res) => {
						uni.showModal({
							title:"数据获取成功",
							content:'内容为：'+res.data+'',
							showCancel:false
						})
					}
				})
			}
		}
	}
}



